<template>
  <div class="wrap">
    <el-card>
      <dqd-table
        :isPagination="true"
        :table-data="tableData"
        :table-label="tableLabel"
        :pagination="pagination"
        :isSelection='false'
        :isIndex='true'
        @handleCurrentChange = "handleCurrentChange"
      >
      </dqd-table>
    </el-card>
  </div>
</template>

<script>
import dqdTable from '../../components/common/table'
export default {
  name: 'userList',
  data () {
    return {
      tableData: [],
      totalNumber: 0,
      tableLabel: [
        {
          label: '序号',
          param: 'index',
          align: 'center',
          type: 'index'
        },
        {
          label: 'ID',
          param: 'id',
          align: 'center',
          width: '120'
        },
        {
          label: '用户名',
          param: 'username',
          align: 'center'
        },
        {
          label: '发表数',
          param: 'publish_num',
          align: 'center'
        },
        {
          label: '评论数',
          param: 'comment_num',
          align: 'center'
        },
        {
          label: '点赞数',
          param: 'up_num',
          align: 'center'
        },
        {
          label: 'IP地址',
          param: 'ip',
          align: 'center',
          width: '230'
        },
        {
          label: '上一次登录时间',
          param: 'last_login_time',
          align: 'center',
          width: '150'
        },
        {
          label: '操作',
          type: 'operation',
          list: [
            {
              label: '预览',
              render: (row) => { // 按钮动态文案
                if (row.status === 1) {
                  return '解封'
                } else if (row.status === 0) {
                  return '封禁'
                }
              },
              formatter: (row) => { // 按钮动态背景色
                return row.status === 1 ? 'success' : 'danger'
              },
              func: this.handleCloser
            }
          ]
        }
      ],
      pagination: {
        pageSize: 20,
        pageNum: 1,
        total: 0
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    handleCurrentChange (val) {
      this.pagination.pageNum = val
      this.getData()
    },
    handleCloser (row, index) { // status: 0:封禁， 1:解封
      let status = row.status === 1 ? 0 : 1
      let title = row.status === 1 ? '解封' : '封禁'
      this.$confirm(`确定${title}用户？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.get(`/admin/setUserStatus?user_id=${row.id}&status=${status}`).then((res) => {
          let data = res.data
          if (data.code === 0) {
            this.$message({
              type: 'success',
              message: `${title}成功!`
            })
            this.getData()
            // this.$set(this.tableData[index], 'status', Number(data.data.status))
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: `已取消${title}`
        })
      })
    },
    getData () {
      this.$axios.get(`http://test-admin-dstock.dongqiudi.net/admin/getUserList?page=${this.pagination.pageNum}&size=${this.pageSize ? this.pageSize : 20}`).then((res) => {
        let data = res.data
        if (data.code === 0) {
          this.tableData = data.data.list
          this.pagination.total = data.data.total
        }
      })
    }
  },
  components: {
    dqdTable
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>

</style>



